<template>
	<main>
		<div class="register_top">
			<span class="return" @click="go"></span>
			<router-link to="/regist1"><span class="one"><</span></router-link>
			<span class="two">手机号验证</span>
		</div>
		<div class="register_con"> 
			<p>你会收到一条带验证码的短信，请输入验证码：</p>
			<div class="test">
				<input type="text" placeholder="请输入验证码" id="txt">
				<time>{{time}}s</time>
			</div>
			<div @click="next">
				<router-link :to="msg" class="next" @click="next">下一步</router-link>
			</div>
			
		</div>

	</main>
</template>
<style scoped>
*{
	text-decoration: none;
}
	main{
		position: fixed;
		top:0;
		left:0;
		background: #fff;
		width:100%;
		height:100%;
		z-index:31;
		font-family: "微软雅黑";
		/* background: url('../../assets/image/big_bg.jpg'); */
		box-sizing: border-box;
	}
		.two{
		position: absolute;
		left: 50px;
	}
	.one{
		color: #FFFFFF;
	}
	.register_top{
		color:#fff;
		padding: 20px;
		background: #d33a31;
	}
	.register_con{
		padding: 20px;
		width:100%;
		box-sizing: border-box;
	}
	.register_con p{
        color: #8B8787;
		font-size: 14px;
		margin-bottom: 50px;
	}
	.register_con .test{
		border-bottom: 1px solid #ccc;
		padding-bottom: 6px;
		margin-bottom: 20px;
	}
	.register_con input{
		border:none;
		background: none;
		outline: none;
		padding-left: 4px;
		width:80%;
	}
		.register_con input::-webkit-input-placeholder {
        /* placeholder颜色  */
        color: #A6A0A0;
        /* placeholder字体大小  */
         font-size: 12px;
         /* placeholder位置  */
        /* text-align: right; */
     }
	.register_con i{
		color:#999;
	}
	.register_con time{
		font-size: 12px;
		color:#999;
	}
	.register_con .next{
		background: #d33a31;
		color:#fff;
		border-radius: 20px;
		width:100%;
		height:44px;
		line-height: 44px;
		text-align: center;
		border:none;
		outline: none;
		margin:40px 0;
		font-size: 14px;
		display:inline-block;
		text-decoration: none;
	}
</style>
<script>
	export default{
		data(){
			return{
				time:60,
				msg:"",
				flag:false,

			}
		},
		 mounted(){
		 	// this.loadMore();
    },
		methods:{
			go(){
				this.$router.go(-1);
			},
			next(){
				if(document.getElementById("txt").value==''){
					this.msg="#";
					this.$messagebox({
							title: '提示',
							message: '请输入验证码',
							showCancelButton: true,
							confirmButtonText:"发送验证码",
							cancelButtonText:"取消"
							}).then(action => {
								if(action == 'confirm'){
									console.log('重新发送')
									this.time=60
									this.$messagebox({
							title: '提示',
							message: '验证码为123456',
							showCancelButton: false,
							})
								}else{
									console.log('取消')
									this.time=60;
								}
							})
				}else if(document.getElementById("txt").value!="123456"){
					this.msg="#";
					this.$messagebox({
							title: '提示',
							message: '验证码错误',
							showCancelButton: true,
							confirmButtonText:"重新发送验证码",
							cancelButtonText:"取消"
							}).then(action => {
								if(action == 'confirm'){
									console.log('重新发送')
									this.time=60
									this.$messagebox({
							title: '提示',
							message: '验证码为123456',
							showCancelButton: false,
							})
								}else{
									console.log('取消')
									this.time=60;
								}
							})
				}else if(this.flag==false){
					this.flag=true;
					this.$messagebox({
							title: '提示',
							message: '验证成功',
							showCancelButton: true,
							confirmButtonText:"确认",
							cancelButtonText:"取消"
							}).then(action => {
								if(action == 'confirm'){
									console.log('重新发送')
									this.time=60
									this.msg="/regist2";
								}else{
									console.log('取消')
									this.time=60;
								}
							})
				}
				if(this.flag==true){
					this.msg="/regist2";
				}
			},
			// loadMore() {
			// 	var a=setInterval(() => {
			// 		this.time=this.time-1;
			// 		if(this.time<=0){
			// 			clearInterval(a);
			// 			this.$messagebox({
			// 				title: '已超时',
			// 				message: '重新发送验证码',
			// 				showCancelButton: true,
			// 				confirmButtonText:"重新发送",
			// 				cancelButtonText:"取消"
			// 				}).then(action => {
			// 					if(action == 'confirm'){
			// 						console.log('重新发送')
			// 						this.time=3

			// 					}else{
			// 						console.log('取消')
			// 						this.time=3;
			// 					}
			// 				})
			// 		}
			// 	}, 1000);
			// }
		}

	}
</script>


