<template>
	<main>
		<div class="register_top">
			<span class="return" @click="go"></span>
			<router-link to="/regist3"><span class="one"><</span></router-link>
			<span class="two">设置昵称</span>
		</div>
		<div class="register_con"> 
			<div class="tig">
				<img src="../../../static/images/pic2.jpg"/>
				<p>取一个昵称，让大家记住你</p>
			</div>
			<div class="test">
				<input type="text" placeholder="请输入昵称"/>
			</div> 
			<router-link to="/recomment" ><a class="submit">开启云音乐</a></router-link>
			
			<div class="protocol">
				<div>
					<input type="checkbox" checked="checked">
				    <span>同意以下协议<a href="">《网易云音乐服务条款》</a></span>
				</div>
			</div>
		</div>
	</main>
</template>
<style scoped>
*{
	text-decoration: none;
}
	main{
		position: fixed;
		top:0;
		left:0;
		background: #fff;
		width:100%;
		height:100%;
		z-index:30;
		font-family: "微软雅黑";
		/* background: url('../../assets/image/big_bg.jpg'); */
		box-sizing: border-box;
	}	
	.two{
		position: absolute;
		left: 50px;
	}
	.one{
		color: #FFFFFF;
	}
	.test{
		padding: 5px;
		border-bottom: 1px solid #B92828;
	}
	.test input::-webkit-input-placeholder {
        /* placeholder颜色  */
        background: linear-gradient(to right, red, blue);
        -webkit-background-clip: text;
        color: transparent;
        /* placeholder字体大小  */
         font-size: 12px;
         /* placeholder位置  */
        /* text-align: right; */
     }
	.register_top{
		color:#fff;
		padding: 20px;
		background: #d33a31;
	}
	.register_con{
		padding: 20px;
		width:100%;
		box-sizing: border-box;
	}
	.register_con .tig{
		text-align: center;
		width:100%;
	}
	.register_con .tig img{
		width:120px;
		border-radius: 50%;
		border: 2px dashed #D11E1E;
	}
	.register_con .tig p{
		text-align: center;
		background: linear-gradient(to right, red, blue);
        -webkit-background-clip: text;
        color: transparent;
		line-height: 80px;
	}
	.register_con .tel,.register_con .pass{
		border-bottom: 1px solid #ccc;
		padding-bottom: 6px;
		margin-bottom: 20px;
	}
	.register_con input{
		border:none;
		background: none;
		outline: none;
		padding-left: 4px;
		width:80%;
	}
	.register_con i{
		color:#999;
	}
	.register_con .submit{
		background: #d33a31;
		color:#fff;
		border-radius: 20px;
		width:100%;
		height:44px;
		line-height: 44px;
		text-align: center;
		border:none;
		outline: none;
		margin:40px 0;
		font-size: 14px;
		display:inline-block;
		text-decoration: none;
	}
	.protocol{
		display: flex;
	}
	.protocol input{
		width: 15px;
	}
	.protocol div{
		position: relative;
		top: 0;
		bottom: 0;
		margin: auto;

	}
</style>
<script>
	export default{
		data(){
			return{
				select:true
			}
		},
		methods:{
			go(){
				this.$router.go(-1);
			},
			changeSelect(){
				this.select=!this.select;
			}
		}
	}
</script>


