<template>
  <main>
    <div class="slide" >
      <div class="top">
         <router-link to=/slide_right>
          <h3><</h3>
         </router-link>
        <p>歌手分类</p>
      </div>
  <mt-navbar v-model="active"><!-- 点击对应选项卡获取对应选项id赋值给active，然后根据active的值获取对应id的内容 -->
  <mt-tab-item id="tab-container1">华语歌手</mt-tab-item>
  <mt-tab-item id="tab-container2">欧美歌手</mt-tab-item>
  <mt-tab-item id="tab-container3">日韩歌手</mt-tab-item>
  <mt-tab-item id="tab-container4">其他歌手</mt-tab-item>
  <mt-tab-item id="tab-container5">热门歌手</mt-tab-item>
  </mt-navbar>
      <mt-tab-container v-model="active" class="list">
        <mt-tab-container-item id="tab-container1">
          <mt-cell v-for="(v,i) in msg" :key="i" :title="v.singer"></mt-cell>
        </mt-tab-container-item>
       <mt-tab-container-item id="tab-container2">
          <mt-cell v-for="(v,i) in msg" :key="i" :title="v.songs"></mt-cell>
        </mt-tab-container-item>
        <mt-tab-container-item id="tab-container3">
          <mt-cell v-for="(v,i) in msg" :key="i" :title="v.singer"></mt-cell>
        </mt-tab-container-item>
        <mt-tab-container-item id="tab-container4">
          <mt-cell v-for="(v,i) in msg" :key="i" :title="v.songs"></mt-cell>
        </mt-tab-container-item>
        <mt-tab-container-item id="tab-container5">
          <mt-cell v-for="(v,i) in msg" :key="i" :title="v.singer"></mt-cell>
        </mt-tab-container-item>
      </mt-tab-container>


<!-- <mt-navbar v-model="selected" >
  <mt-tab-item id="1">选项一</mt-tab-item>
  <mt-tab-item id="2">选项二</mt-tab-item>
</mt-navbar>
<mt-tab-container v-model="selected">
  <mt-tab-container-item id="1">
    sadasdasd
  </mt-tab-container-item>
  <mt-tab-container-item id="2">
    213123131
  </mt-tab-container-item>
</mt-tab-container> -->


    </div>
  </main>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      msg:[''],
      active:[''],
      selected:[''],
    }
  },
  mounted(){
      /*console.log(musicObject);*/
      this.$http.get('./static/recomment.json',{
        params:{
        }
      }).then(function(res){
        this.msg=res.data.songlist;
        console.log('')
      },function(){
      });
    },
  methods:{
    ch(mes){
      this.active=mes;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
  margin: 0;
  padding: 0;
  list-style: none;
}
.slide{
    z-index: 9;
    position: fixed;
    top:0;
    left:0;
    height:77%;
    width:100%;
  }
.slide .top{
width: 100%;
height: 50px;
background-color: #DF2E2E;
background-size: 100% 100%;
position: relative;
color: white;
display: flex;
}
.top h3{
  position: absolute;
  line-height: 50px;
  left: 5%;
  color: #FFFFFF;
}
.top p{
  position: absolute;
  line-height: 50px;
  left: 15%;
}
.list{
 overflow-y: scroll;
}

.slide .list{
  height: 100%;
  background-color: #fff;
}

</style>



