<template>
  <main>
    <div class="city_searchBox" ref="search">    // 搜索框部分
    <input class="" type="text" v-model="searchVal" placeholder="">
</div>
<div class="city_list" ref="cityList" :style="{height: cityListHeight + 'px'}">    // 国家/城市列表部分
    <mt-index-list>
        <mt-index-section v-for="(item,index) in newCityList" :key="index" :index="item.cityTp"
                          v-show="regListItem(item)">
            <mt-cell v-for="(list,indexList) in item.data" :key="indexList" :title="list.cityNamw"
                      v-show="regItem(list)">
 
            </mt-cell>
        </mt-index-section>
    </mt-index-list>
</div>

  </main>
</template>

<script>
export default {
  data () {
    return {
      searchVal:[],
      cityListHeight:[],
      newCityList:[],
      cityData: [{
                        "cityTp": "A",
                        "cityCode": "ALB",
                        "cityPl": "aerbaniva",
                        "cityNamw": "阿尼巴尼亚"
                    },
                    {
                        "cityTp": "A",
                        "cityCode": "AUS",
