                    },
                    {
                        "cityTp": "D",
                        "cityCode": "DRK",
                        "cityPl": "Dhaoxian",
                        "cityNamw": "d鲜"
                    },
                    {
                        "cityTp": "E",
                        "cityCode": "ERK",
                        "cityPl": "Echaoxian",
                        "cityNamw": "E朝鲜"
                    },
                    {
                        "cityTp": "F",
                        "cityCode": "FPRK",
                        "cityPl": "Fchaoxian",
                        "cityNamw": "F朝鲜"
                    },
                    {
                        "cityTp": "G",
                        "cityCode": "GPRK",
                        "cityPl": "Gchaoxian",
                        "cityNamw": "G朝鲜"
                    },
                    {
                        "cityTp": "H",
                        "cityCode": "HPRK",
                        "cityPl": "HcHhaoxian",
                        "cityNamw": "H朝鲜"
                    },
                    {
                        "cityTp": "I",
                        "cityCode": "IPRK",
                        "cityPl": "Ichaoxian",
                        "cityNamw": "I朝鲜"
                    },
                    {
                        "cityTp": "J",
                        "cityCode": "JPRK",
                        "cityPl": "Jchaoxian",
                        "cityNamw": "J朝鲜"
                    },
                    {
                        "cityTp": "K",
                        "cityCode": "KPRK",
                        "cityPl": "Kchaoxian",
                        "cityNamw": "K朝鲜"
                    },
                    {
                        "cityTp": "L",
                        "cityCode": "LPRK",
                        "cityPl": "Lchaoxian",
                        "cityNamw": "L朝鲜"
                    },{
                        "cityTp": "M",
                        "cityCode": "MPRK",
                        "cityPl": "Mchaoxian",
                        "cityNamw": "M朝鲜"
                    },{
                        "cityTp": "N",
                        "cityCode": "NPRK",
                        "cityPl": "Nchaoxian",
                        "cityNamw": "N朝鲜"
                    },{
                        "cityTp": "O",
                        "cityCode": "PRK",
                        "cityPl": "chaoxian",
                        "cityNamw": "朝鲜"
                    },{
                        "cityTp": "P",
                        "cityCode": "PRK",
                        "cityPl": "Pchaoxian",
                        "cityNamw": "P朝鲜"
                    },{
                        "cityTp": "Q",
                        "cityCode": "QPRK",
                        "cityPl": "QPchaoxian",
                        "cityNamw": "QP朝鲜"
                    },{
                        "cityTp": "R",
