                        "cityNamw": "VP朝鲜"
                    },{
                        "cityTp": "W",
                        "cityCode": "WPRK",
                        "cityPl": "WPchaoxian",
                        "cityNamw": "WP朝鲜"
                    },{
                        "cityTp": "X",
                        "cityCode": "XPRK",
                        "cityPl": "XPchaoxian",
                        "cityNamw": "XP朝鲜"
                    },{
                        "cityTp": "Y",
                        "cityCode": "YPRK",
                        "cityPl": "YPchaoxian",
                        "cityNamw": "YP朝鲜"
                    },{
                        "cityTp": "Z",
                        "cityCode": "ZPRK",
                        "cityPl": "ZPchaoxian",
                        "cityNamw": "ZP朝鲜"
                    }],
                    cx:[''],

    }
  },
  mounted(){
    
      /*console.log(musicObject);*/
      this.$http.get('./static/data/list.json',{
        params:{
        }
      }).then(function(res){
         this.cx=res.data.cityData;
         console.log(this.cx[0].cityCode)
         console.log(this.cityData[0].cityCode==this.cx[0].cityCode)
         console.log(JSON.stringify(this.cx)==JSON.stringify(this.cityData))
      },function(){
      });
     
            //将cityData数据类型转换成mintui相应类型格式
            let map = {}, dest = [];
            for (let i = 0; i < this.cityData.length; i++) {
                let ai = this.cityData[i];
                if (!map[ai.cityTp]) {
                    dest.push({
                        cityTp: ai.cityTp,
                        data: [ai]
                    });
                    map[ai.cityTp] = ai;
                } else {
                    for (let j = 0; j < dest.length; j++) {
                        let dj = dest[j];
                        if (dj.cityTp === ai.cityTp) {
                            dj.data.push(ai);
                            break;
                        }
                    }
                }
            }
            this.newCityList = dest;
             // 计算cityList的高度
            let winHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
            this.cityListHeight  = winHeight  - this.$refs.cityList.getBoundingClientRect().top
    },

  methods:{
    //模糊搜索规则
            regItem(value){
                let regVal = new RegExp(this.searchVal);
                return regVal.test(value.cityPl) || regVal.test(value.cityNamw);
            },
            regListItem(value){
                let regVal = new RegExp(this.searchVal);
                for (let i=0;i<value.data.length;i++){
                    if (regVal.test(value.data[i].cityPl) || regVal.test(value.data[i].cityNamw)){
                        return true;
                    }
                }
            }

  }
}
</script>





